<template>
  <div class="editTree">
    <h1>菜单编辑</h1>
    <div class="edit-wapper">
      <i class="el-icon-plus"
        @click="openDialog(STATE.add)"></i>
      <el-tree :data="tree"
        :props="defaultProps"
        :expand-on-click-node="false">
        <span class="custom-tree-node"
          slot-scope="{ node, data }">
          <span class="node">{{ data.menuName }}</span>
          <span>
            <i class="el-icon-plus"
              @click="openDialog(STATE.add,data)"></i>
            <i class="el-icon-edit-outline"
              @click="openDialog(STATE.update,data)"></i>
            <i class="el-icon-delete"
              @click="() => remove(data.id)"></i>
          </span>
        </span>
      </el-tree>
    </div>
    <el-dialog :title="dialogTitle"
      :visible.sync="showDialog">
      <el-form :model="editinfo"
        label-position="right"
        label-width="150px">
        <el-form-item label="菜单名称">
          <el-input v-model="editinfo.menuName"></el-input>
        </el-form-item>
        <el-form-item label="菜单描述">
          <el-input v-model="editinfo.menuDesc"></el-input>
        </el-form-item>
        <el-form-item label="排序权重">
          <el-input v-model="editinfo.ordor"></el-input>
        </el-form-item>
        <el-form-item label="路径">
          <el-input v-model="editinfo.url"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer"
        class="dialog-footer">
        <el-button @click="showDialog = false">取 消</el-button>
        <el-button type="primary"
          @click="confirm">
          确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { menuapi } from '@/components/api';

const Template = {
  menuDesc: null,
  menuName: null,
  ordor: null,
  id: null,
  url: null,
};

export default {
  data() {
    return {
      STATE: { add: 0, update: 1 },
      defaultProps: { children: 'children' },
      showDialog: false,
      state: null,
      editinfo: JSON.parse(JSON.stringify(Template)),
    };
  },
  computed: {
    dialogTitle() {
      let title = null;
      if (this.state === this.STATE.add) {
        title = '添加菜单';
      } else if (this.state === this.STATE.update) {
        title = '更新菜单';
      }
      return title;
    },
  },
  asyncComputed: {
    async tree() {
      const data = await menuapi.getSystemMenuTree();
      return data.children;
    },
  },
  watch: {
    showDialog(newVal) {
      if (!newVal) this.editinfo = JSON.parse(JSON.stringify(Template));
    },
  },
  methods: {
    refresh() {
      window.top.location.reload();
    },
    openDialog(STATE, data) {
      if (this.state === this.STATE.add) {
        this.editinfo.parentId = data ? data.id : -1;
      } else if (this.state === this.STATE.update) {
        this.editinfo = JSON.parse(JSON.stringify(data));
      }
      this.state = STATE;
      this.showDialog = true;
    },
    confirm() {
      if (this.state === this.STATE.add) {
        this.add(this.editinfo);
      } else if (this.state === this.STATE.update) {
        this.update(this.editinfo);
      }
    },
    async remove(id) {
      this.$confirm('此操作将永久删除该, 是否继续?', '提示',
        { confirmButtonText: '确定', cancelButtonText: '取消', type: 'warning' })
        .then(async () => {
          if (await menuapi.deleteByIds(id)) {
            this.$message({
              type: 'success',
              message: '删除成功!',
              duration: 500,
              onClose: this.refresh,
            });
          }
        });
    },
    async add(data) {
      if (await menuapi.add(data)) {
        this.$message({
          type: 'success',
          message: '添加成功!',
          duration: 500,
          onClose: this.refresh,
        });
      }
    },
    async update(data) {
      if (await menuapi.update(data)) {
        this.$message({
          type: 'success',
          message: '更新成功!',
          duration: 500,
          onClose: this.refresh,
        });
      }
    },
  },
};
</script>

<style lang="stylus">
.editTree
  margin 10px
  .edit-wapper
    border 1px solid #000
    padding 10px
    .node
      margin-right 10px
    i
      margin-right 5px
</style>
